<template>
  <v-content>
    <div class="align-baseline">
      <h1>Containers</h1>
      <v-spacer />
      <v-dialog v-model="containerNewDialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-btn dark v-bind="attrs" v-on="on"> new </v-btn>
        </template>
        <v-card>
          <v-card-title class="headline grey lighten-2">
            Create New Container
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="image"
                    label="image"
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="containerNewDialog = false"> cansel </v-btn>
            <v-btn text @click="createContainer"> create </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <v-list three-line>
      <v-divider></v-divider>
      <v-list-item v-for="container in containers" :key="container.Id">
        <v-list-item-content>
          <v-list-item-title>
            {{ container.Names[0] }}
          </v-list-item-title>
          <v-list-item-subtitle>
            <v-layout wrap>
              <v-flex>{{ container.Image }}</v-flex>
              <v-flex>{{ container.Id }}</v-flex>
              <v-flex>{{ container.State }}</v-flex>
              <v-flex>{{ container.Status }}</v-flex>
            </v-layout>
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
    </v-list>
  </v-content>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    image: "",
    containers: undefined,
    containersLoaded: false,
    containerNewDialog: false,
  }),
  methods: {
    createContainer() {
      this.containerNewDialog = false;

      this.axios
        .post("/containers", {
          name: this.image,
        })
        .then((response) => {
          console.log(response);
        });
    },
  },
  created() {
    this.axios.get("/containers").then((response) => {
      this.containers = response.data;
      this.containersLoaded = true;
      console.log(this.containers);
    });
  },
};
</script>
